.buttons {
    display: flex;
    flex-wrap: wrap;
}

.buttons__button {
    background-color: transparent;
    color: teal;
    border: none;
    padding: 10px;
    transition: color 0.5s;
}

@media(max-width: 767px){
    .buttons__button{
        flex-basis: 100%;
    }
}

.buttons__button:hover {
    color: hsl(180, 100%, 40%);
}

.buttons__button:active {
    color: hsl(180, 100%, 50%);
}

.buttons__button:disabled {
    color: #ddd;
}

